.main{
	max-width: 1170px;
	height: auto;
	margin: 116px auto 116px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	@media screen and (min-width: 320px) and (max-width: 767px){
		padding-left: 20px;
	}
}
.main__item{
	max-width: 370px;
	height: 305px;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin:0 20px 88px 0;
	@media (max-width: 1206px) {
		width: 315px;;
	}
	@media (max-width: 1034px){
		width: 300px;
	}
}
.main__item-img{
	width: 100px;
	height: 100px;
}
